<template>
    <div class="main-center main-center-2">
        <p class="mod-tit mt25">
            <span class="left-bor"></span>
            <span class="mod-txt">创建孩子信息</span>
        </p>
        <div class="add-child-box">
            <form name="addChildForm">
                <ul class="user-list">
                    <li class="flex-center">
                        <div class="list-item-left"></div>
                        <div class="student-img-box">
                            <img :src="babyImg" alt="">
                            <!--<a href="javascript:void(0)">编辑头像</a>-->
                        </div>
                    </li>
                    <li>
                        <div class="list-item-left">
                            <span class="xing">*</span><span class="list-item-tit">姓名：</span>
                        </div>
                        <div class="list-item-right">
                            <input type="text"
                                   class="ipt"
                                   maxlength="20"
                                   name="childName"
                                   v-required="true"
                                   v-pattern="/^[\u4e00-\u9fa5A-Za-z\s]+$/"
                                   v-model="addChildModel.name"
                                   placeholder="请输入孩子姓名">

                            <div class="wrong-tips"
                                 v-if="addChildForm.childName.$dirty&&addChildForm.childName.$error.required&&!addChildForm.childName.$error.pattern">请输入孩子姓名</div>
                            <div class="wrong-tips"
                                 v-if="addChildForm.childName.$dirty&&addChildForm.childName.$error.pattern&&!addChildForm.childName.$error.required">姓名只能由中文、字母或者空格组成</div>
                        </div>
                    </li>

                    <li>

                        <div class="list-item-left">
                            <span class="xing">*</span><span class="list-item-tit">性别：</span>
                        </div>
                        <div class="list-item-right" style="margin-top: 0.7rem;">
                            <el-radio v-model="addChildModel.sex"
                                      label="1">男宝</el-radio>
                            <el-radio v-model="addChildModel.sex"
                                      label="2">女宝</el-radio>

                        </div>
                    </li>

                    <li>
                        <div class="list-item-left">
                            <span class="list-item-tit">英文名：</span>
                        </div>
                        <div class="list-item-right">
                            <input type="text"
                                   class="ipt"
                                   name="englishName"
                                   maxlength="20"
                                   v-pattern="/^[a-zA-Z\s]+$/"
                                   v-model="addChildModel.englishName"
                                   placeholder="请输入孩子英文名">
                            <div class="wrong-tips"
                                 v-if="addChildForm.englishName.$dirty && addChildForm.englishName.$invalid">请输入英文</div>
                        </div>
                    </li>
                    <li>

                        <div class="list-item-left">
                            <span class="xing">*</span><span class="list-item-tit">出生年月：</span>
                        </div>
                        <div class="list-item-right">
                            <el-date-picker
                                    :default-value="aaa"
                                    name="birthday"
                                    v-required="true"
                                    v-model="addChildModel.birthday"
                                    type="date"
                                    placeholder="请选择孩子出生年月">
                            </el-date-picker>
                            <div class="wrong-tips"
                                 v-if="addChildForm.birthday.$dirty && addChildForm.birthday.$invalid">请选择孩子出生年月</div>
                        </div>
                    </li>


                    <li>
                        <div class="list-item-left">
                            <span class="xing">*</span><span class="list-item-tit">年级：</span>
                        </div>
                        <div class="list-item-right">
                            <grade-list v-model="addChildModel.gradeTypeId"
                                        name="gradeTypeId"
                                        v-required="true"></grade-list>
                            <div class="wrong-tips"
                                 v-if="addChildForm.gradeTypeId.$dirty && addChildForm.gradeTypeId.$invalid">请选择孩子的年级</div>
                        </div>
                    </li>

                    <li>
                        <div class="list-item-left">
                            <span class="list-item-tit">培训时长：</span>
                        </div>
                        <div class="list-item-right">
                            <training-time-list name="trainingTime"
                                                v-model="addChildModel.trainingTime"></training-time-list>
                        </div>
                    </li>




                    <li>
                        <div class="list-item-left">
                            <span class="list-item-tit"></span>
                        </div>
                        <button class="submit"
                                :style="addChildForm.$invalid?{background:'#ddd',cursor:'not-allowed'}:{}"
                                @click="addChild($event,addChildForm.$invalid)"
                                element-loading-text="操作中请稍后。。"
                                element-loading-background="rgba(0, 0, 0, 0.7)"
                                v-loading.fullscreen="submitAble">确定</button>
                    </li>
                </ul>
            </form>
        </div>
    </div>
</template>

<script>
    import gradeList from '@/common/gradeList.vue'
    import trainingTimeList from '@/common/trainingTimeList.vue'
    import {initFormErrorObj} from '@/assets/js/validationPlugin'
    import {mapActions} from 'vuex'
    export default {
        data: () => ({
            aaa:new Date('2012-06-30'),
            ...initFormErrorObj('addChildForm', ['childName','birthday','gradeTypeId','englishName']),
            submitAble:false,
            addChildModel: {
                name:'',
                sex:'1',//1男2女
                englishName:'',
                birthday:'',
                gradeTypeId:'7',
                trainingTime:'2'
            },
            addChildFormRules: {
                name: [
                    {required: true, message: '请输入孩子姓名'}
//              { pattern:/^\d{11}$/, message: '请输入11位手机号'}
                ],
                birthday: [
                    {required: true, message: '请选择孩子出生年月'}
                ],
                gradeTypeId:[
                    {required: true, message: '请选择孩子的年级'}
                ]
            }
        }),
        methods:{
            ...mapActions('child',['studentLoginActions','addChildActions']),
            ...mapActions(['getHomeCardActions']),
            addChild(e,invalid){
                e.preventDefault()
                if(this.submitAble || invalid){
                    return
                }
                this.submitAble = true
                this.addChildRequest()
            },
            addChildRequest(){
                this.addChildActions(this.addChildModel).then((res)=>{
                    this.submitAble = false
                    let {error_code,data} = res;
                    if(!error_code){
                        if(data.length<=1){
                            let studentId = data[data.length-1].student_id
                            this.loginByStudentId(studentId)
                        }else{
                            this.$router.push({
                                name:'sign_in_success'
                            })
                        }
                        this.$message.success('添加孩子成功')
                    }
                },(err)=>{
                    this.submitAble = false
                })
            },
            loginByStudentId(studentId){
                let params = {student_id: studentId}
                this.studentLoginActions(params).then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                        this.$store.dispatch('account/setStudentIdCookie',studentId)
                        //新增第一个学生后用这个学生登录，并且要更新一下首页数据
                        this.$bus.$emit('getHomeCard',true)
                        this.$router.push({
                            name:'sign_in_success'
                        })
//                        this.getHomeCardActions().then((res)=>{
//                            this.$router.push({
//                                name:'sign_in_success'
//                            })
//                        })
                    }
                },(err)=>{

                })
            }
        },
        mounted(){
        },
        computed: {
            babyImg(){
                let result = ''
                result = this.addChildModel.sex==1?require('@/assets/img/boy-img.jpg'):require('@/assets/img/girl-img.jpg')
                return result
            }
        },
        components: {
            gradeList,
            trainingTimeList
        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped>
    .addChild {
        margin: 0 auto;
        width: 400px;
    }

    .xing {
        color:red;
    }
    .addChildList > li{
        margin-bottom:15px
    }
</style>
