<template>
  <div class="main-center">
    <div v-if="childInfo.name">
      <div class="survey">
        <img :src="childInfo.head_portrait_url" alt="" />
        <div class="student-edit">
          <el-tooltip placement="left" popper-class="stuedit-tooltip">
            <div slot="content" class="con">
              {{ childInfo.name || "-" }}
            </div>
            <div class="stu-name">{{ childInfo.name || "-" }}</div>
          </el-tooltip>
          <p class="age">学号:{{childInfo.code}}</p>
        </div>
        <ul class="survey-list">
          <li>
            <div class="top">
              <span class="count">{{ navCard.arrive_count || "-" }}</span>次
            </div>
            <div class="bottom">到课次数</div>
          </li>
          <li>
            <div class="top">
              <span class="count">{{ navCard.submit_task_count || "-" }}</span>个
            </div>
            <div class="bottom">已参与学习</div>
          </li>
          <li>
            <div class="top">
              <span class="count">{{ navCard.pay_transaction_record_count || "-" }}</span>次
            </div>
            <div class="bottom">已学课次</div>
          </li>
          <li>
            <div class="top">
              <span class="count">{{ navCard.time_total || "-" }}</span>{{ navCard.time_name }}
            </div>
            <div class="bottom">累计听读</div>
          </li>
        </ul>
      </div>
      <div>
        <ul class="user-list">
          <li>
            <span class="user-label">姓名：</span>
            <div class="label-info">
              <span>{{childInfoModel.name}}</span>
            </div>
          </li>
          <li>
            <span class="user-label">英文名：</span>
            <div class="label-info">
              <span>{{childInfoModel.english_name}}</span>
            </div>
          </li>
          <li>
            <span class="user-label">性别：</span>
            <div class="label-info">
              <span>{{childInfoModel.sex_name}}</span>
            </div>
          </li>
          <li>
            <span class="user-label">在读年级：</span>
            <div class="label-info">
              <span>{{childInfoModel.grade_type_name}}</span>
            </div>
          </li>
          <li>
            <span class="user-label">绑定手机：</span>
            <div class="label-info">
              <span>{{childInfoModel.phone}}</span>
            </div>
          </li>
          <li>
            <span class="user-label">在读学校：</span>
            <div class="label-info">
              <span>{{childInfoModel.attending_school_name}}</span>
            </div>
          </li>
          <li>
            <span class="user-label">家庭住址：</span>
            <div class="label-info">
              <span>{{childInfoModel.address}}</span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="has-not-child-box" v-if="!childInfo.name">
      <img src="@/assets/img/empty-img.png" alt="" />
      <p class="login-tit">暂无孩子</p>
      <!-- <p class="login-txt">您可以选择创建一个孩子</p>
      <button @click="goAddChild">+创建孩子信息</button> -->
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  data: () => ({
    childInfoModel: {
      name: "",
      sex: "1", //1男2女
      englishName: "",
      birthday: "",
      gradeTypeId: "7",
      trainingTime: "2",
    },
    countModel: {},
  }),
  methods: {
    ...mapActions("child", ["getStudentInfoActions"]),
    goAddChild() {
      this.$router.push({
        name: "addChild",
      });
    },
    //手动转换一些类型
    initParams(data) {
      data.sex = Number(data.sex);
      data.gradeTypeId = data.grade;
      data.englishName = data.english_name;
      data.trainingTime = data.training_time;
      data.sex = data.sex + "";
    },
    getStudentInfo(value) {
      if (value) {
        let params = {
          student_id: value
        }
        this.getStudentInfoActions(params).then((res) => {
          let { error_code, data } = res;
          if (!error_code) {
            this.initParams(data);
            this.childInfoModel = data;
          }
        });
      }
    },
  },
  mounted() {
    setTimeout(()=>{
      this.getStudentInfo(this.childInfo.student_id)  
    })
  },
  computed: {
    ...mapGetters("child", ["childInfo"]),
    ...mapGetters(['navCard'])
  },
  components: {},
  created() {},
  watch:{}
};
</script>

<style lang="postcss" scoped>
.childInfo {
  margin: 0 auto;
  width: 600px;
}

.xing {
  color: red;
}
.childInfoList > li {
  margin-bottom: 15px;
}
.childTop {
  display: flex;
  margin-bottom: 20px;
  justify-content: space-between;
}
</style>
