<template>
    <el-select v-model="val"
               placeholder="请选择孩子的年级">
        <el-option
                v-for="item in gradeList"
                :key="item.grade_id"
                :label="item.grade_name"
                :value="item.grade_id">
        </el-option>
    </el-select>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        props: {
            value: {
                type: String
            }
        },
        mounted() {
            this.getGradeList()
        },
        data() {
            return {
                val:'',
                gradeList: []
            };
        },
        watch: {
            val:{
                handler(nv){
                    if(nv){
                        this.$emit('input',nv)
                    }else{
                        this.$emit('input','')
                    }
                },
                immediate:true
            },
            value:{
                handler(nv){
                    this.val = nv
                },
                immediate:true
            }
        },
        methods: {
            ...mapActions('child',['getGradeListActions']),
            getGradeList(){
                this.getGradeListActions().then((res)=>{
                    let {error_code,data} = res
                    if(!error_code){
                        this.gradeList = data
                    }
                },(err)=>{})
            }
        }
    };
</script>
