<template>
    <el-select v-model="value"
               placeholder="请选择培训时长">
        <el-option
                v-for="item in trainingTimeList"
                :key="item.type"
                :label="item.name"
                :value="item.type">
        </el-option>
    </el-select>
</template>

<script>
    import {mapActions} from 'vuex'
    export default {
        props: {
            value: {
                type: String
            }
        },
        mounted() {
            this.getTrainingTimeList()
        },
        data() {
            return {
                val:'',
                trainingTimeList: []
            };
        },
        watch: {
            value:{
                handler(nv){
                    this.val = nv
                },
                immediate:true
            },
            val:{
                handler(nv){
                    if(nv){
                        this.$emit('input',nv)
                    }else{
                        this.$emit('input','0')
                    }
                },
                immediate:true
            }
        },
        methods: {
            ...mapActions('child',['getTrainingTimeListActions']),
            getTrainingTimeList(){
                this.getTrainingTimeListActions().then((res)=>{
                    let {error_code,data} = res
                    if(!error_code){
                        this.trainingTimeList = data
                        this.trainingTimeList.forEach((item)=>{
                            item.type = item.type+''
                        })
                    }
                },(err)=>{})
            }
        }
    };
</script>
